<template>
  <div id="app">
    <vue-dropzone 
      ref="dropzone" 
      id="dropzone"
      @vdropzone-sending="sendingEvent"
      :options="dropOptions"
      @vdropzone-complete="afterComplete"
    ></vue-dropzone>
    <v-btn @click="removeAllFiles">{{ $t('global.remove_files_title') }}</v-btn>
  </div>
</template>
<script>
import vueDropzone from 'vue2-dropzone'
export default {
  data () {
    let { uploadPath, currentUser } = this.$store.state.AppStore
    return {
      dropOptions: {
        url: uploadPath + '/' + currentUser.user_token,
        maxFilesize: 2, // MB
        maxFiles: 1,
        chunking: false,
        // chunkSize: 500, // Bytes
        thumbnailWidth: 150, // px
        thumbnailHeight: 100,
        headers: { 'USER_NAME': this.$store.state.AppStore.currentUser.user_name },
        addRemoveLinks: true,
        accept (file, done) {
          console.log(file)
          done()
        }
      }
    }
  },
  methods: {
    afterComplete (file) {
      console.log(file)
    },
    sendingEvent (file, xhr, formData) {
      console.log('DEBUG: formData BEFORE ' + JSON.stringify(formData))
      formData.append('fileName', 'NEW_FILE')
      console.log('DEBUG: formData AFTER ' + JSON.stringify(formData))
    },
    removeAllFiles () {
      this.$refs.dropzone.removeAllFiles()
    }
  },
  components: {
    vueDropzone
  }
}
</script>